/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.expression-controls {
  display: flex;
  align-items: stretch; }
  .expression-controls .form-group {
    margin-right: 15px;
    padding-right: 15px;
    border-right: 1px dashed #ddd;
    display: flex !important;
    align-items: center; }
    .expression-controls .form-group h5 {
      font-weight: bold !important;
      margin-right: 5px;
      margin-bottom: 0px; }
    .expression-controls .form-group:last-child {
      border-right: none; }
