/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.query-summary {
  display: flex;
  justify-content: space-between; }
  .query-summary .query-summary__leftItems {
    display: flex;
    align-items: center; }
    .query-summary .query-summary__leftItems > div {
      margin-right: 10px; }
    .query-summary .query-summary__leftItems h3 {
      margin-bottom: 0px; }
  .query-summary .query-summary__rightItems {
    display: flex;
    align-items: center; }

.query-summary {
  font-size: 13px; }
  .query-summary h3 {
    max-width: 1000px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }

@media only screen and (max-width: 1400px) {
  .query-summary {
    font-size: 12px; }
    .query-summary h4 {
      max-width: 800px; } }

@media only screen and (max-width: 1280px) {
  .query-summary h4 {
    max-width: 600px; } }
