/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.coexpression-plot-data-tooltip {
  max-width: none !important; }
  .coexpression-plot-data-tooltip div {
    font-size: 12px; }

.coexpression-plot-toolbar-elt {
  display: inline-block !important;
  margin-right: 10px !important; }

