/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.Safari .oncoprintjs__scroll_div {
  position: static !important; }

.oncoprintjs__scroll_div::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: rgba(0, 0, 0, 0);
  -webkit-border-radius: 100px; }

.oncoprintjs__scroll_div::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.21);
  -webkit-border-radius: 100px;
  width: 8px;
  height: 8px; }

.oncoprintjs__scroll_div::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.3); }

.oncoprintjs__scroll_div::-webkit-scrollbar-thumb:active {
  background: rgba(0, 0, 0, 0.42);
  -webkit-border-radius: 100px; }

.oncoprintAddClinicalTracks > .tab-content {
  padding: 10px 0 5px 0 !important; }
  .oncoprintAddClinicalTracks > .tab-content > .msk-tab {
    padding-left: 5px !important;
    padding-right: 5px !important; }
    .oncoprintAddClinicalTracks > .tab-content > .msk-tab .ReactVirtualized__Table > * {
      width: 100% !important; }

.oncoprintAddClinicalTracksDropdown .rc-tooltip-arrow {
  border: none !important; }

.oncoprintDropdownCount {
  font-size: 11px;
  background: #ddd;
  border-radius: 40px;
  padding: 2px 6px; }

.oncoprintContainer {
  border: none !important;
  padding: 0 !important; }
  .oncoprintContainer #oncoprintDiv .oncoprintjs__track_options__toggle_btn_img {
    top: 4px; }
  .oncoprintContainer #oncoprintDiv .oncoprintjs__track_options__toggle_btn_img,
  .oncoprintContainer #oncoprintDiv .oncoprintjs__header__toggle_btn_img {
    border-width: 0px !important;
    border-radius: 5px;
    position: relative; }
    .oncoprintContainer #oncoprintDiv .oncoprintjs__track_options__toggle_btn_img:hover,
    .oncoprintContainer #oncoprintDiv .oncoprintjs__header__toggle_btn_img:hover {
      background-color: #eee; }
  .oncoprintContainer #oncoprintDiv .oncoprintjs__track_options__dropdown,
  .oncoprintContainer #oncoprintDiv .oncoprintjs__header__dropdown {
    border: 1px solid rgba(0, 0, 0, 0.15) !important;
    margin: 0 !important;
    padding: 0 !important;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
    border-radius: 5px;
    z-index: 10000; }
    .oncoprintContainer #oncoprintDiv .oncoprintjs__track_options__dropdown li,
    .oncoprintContainer #oncoprintDiv .oncoprintjs__header__dropdown li {
      padding: 2px 5px; }
      .oncoprintContainer #oncoprintDiv .oncoprintjs__track_options__dropdown li.oncoprintjs__track_options__separator, .oncoprintContainer #oncoprintDiv .oncoprintjs__track_options__dropdown li.oncoprintjs__header__separator,
      .oncoprintContainer #oncoprintDiv .oncoprintjs__header__dropdown li.oncoprintjs__track_options__separator,
      .oncoprintContainer #oncoprintDiv .oncoprintjs__header__dropdown li.oncoprintjs__header__separator {
        border-top: 1px solid #dddddd !important; }
      .oncoprintContainer #oncoprintDiv .oncoprintjs__track_options__dropdown li,
      .oncoprintContainer #oncoprintDiv .oncoprintjs__header__dropdown li {
        border-width: 0 !important; }
      .oncoprintContainer #oncoprintDiv .oncoprintjs__track_options__dropdown li:hover,
      .oncoprintContainer #oncoprintDiv .oncoprintjs__header__dropdown li:hover {
        background: #fff !important;
        text-decoration: underline; }

.oncoprintContainer {
  opacity: 0;
  max-width: 95vw; }
  .oncoprintContainer.fadeIn {
    transition: opacity 0.75s ease-in-out;
    opacity: 1; }

.tabMessageContainer .alert {
  margin-bottom: 5px; }

#wideGeneBoxValidationStatus > div {
  white-space: normal;
  width: 100%;
  max-width: 555px; }

.quick_oql_edit {
  display: flex;
  margin-top: 10px;
  margin-bottom: 10px;
  align-items: flex-start; }
  .quick_oql_edit .oqlValidationContainer {
    min-height: 27px; }
  .quick_oql_edit div {
    justify-content: left !important; }
