/* Colors */
.portalWidth {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 400px; }

.framedImage {
  margin: 20px 0;
  border: 1px solid #eee !important;
  border-radius: 5px;
  padding: 10px;
  display: block;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.21); }

.cancer-summary--chart-container {
  padding: 0px 0px;
  margin-top: 10px;
  position: relative;
  width: 100%;
  height: 600px; }

.cancer-type--summary-tab-tooltip {
  background-color: white !important;
  border: 1px solid #ddd !important;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  position: absolute;
  display: block;
  padding: 8px !important;
  min-width: 300px;
  max-width: 500px; }
  .cancer-type--summary-tab-tooltip .table {
    margin-bottom: 0px !important; }
    .cancer-type--summary-tab-tooltip .table > thead > tr > th {
      font-size: 12px;
      line-height: 1; }
  .cancer-type--summary-tab-tooltip th:first-child,
  .cancer-type--summary-tab-tooltip td:first-child {
    border-right: 2px solid #ddd; }

.cancer-summary--main-options {
  margin-left: 5px;
  margin-bottom: 15px; }

.cancer-summary--chart-buttons {
  margin-bottom: 15px; }

.cancer-summary--form-controls {
  border: 1px solid #ddd;
  margin-bottom: 10px; }
  .cancer-summary--form-controls > table > tr > td {
    padding-left: 13px;
    padding-right: 13px;
    padding-top: 5px;
    padding-bottom: 5px; }
    .cancer-summary--form-controls > table > tr > td.dashed-border-right {
      border-right: 1px dashed #ddd; }
    .cancer-summary--form-controls > table > tr > td.slider-input {
      width: 110px; }
  .cancer-summary--form-controls > table > tr:last-child > td {
    padding-bottom: 10px; }
  .cancer-summary--form-controls .slider-holder {
    display: flex;
    align-items: flex-start;
    width: 235px; }
    .cancer-summary--form-controls .slider-holder > div:first-child {
      flex: 1 1 100%;
      margin-bottom: 0px; }
    .cancer-summary--form-controls .slider-holder > div:last-child {
      margin-left: 25px; }
  .cancer-summary--form-controls .rangeslider-horizontal .rangeslider__fill {
    background-image: -webkit-linear-gradient(top, #89cdef 0, #81bfde 100%);
    background-image: -o-linear-gradient(top, #89cdef 0, #81bfde 100%);
    background-image: linear-gradient(to bottom, #89cdef 0, #81bfde 100%);
    background-repeat: repeat-x; }
  .cancer-summary--form-controls .rangeslider__label-list .rangeslider__label {
    top: 20px; }
  .cancer-summary--form-controls .rangeslider {
    margin: 8px 0 20px; }
  .cancer-summary--form-controls .slider {
    width: 100%; }
  .cancer-summary--form-controls .rangeslider-horizontal .rangeslider__handle {
    width: 20px;
    height: 20px;
    border-radius: 20px; }
    .cancer-summary--form-controls .rangeslider-horizontal .rangeslider__handle:after {
      width: 12px;
      height: 12px;
      top: 3px;
      left: 3px; }

.highlightInput .form-control {
  border-color: #66afe9;
  outline: 0;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6); }
