.studyTagsTooltip {
  max-height: 45vh;
  overflow: auto; }
  .studyTagsTooltip .json-to-table table {
    width: 100%;
    border-collapse: collapse;
    font-family: sans-serif;
    font-size: 0.7rem;
    background-color: #fff; }
  .studyTagsTooltip .json-to-table td,
  .studyTagsTooltip .json-to-table th {
    padding: 5px;
    border: 1px solid #e2e2e2; }
  .studyTagsTooltip .json-to-table td {
    text-align: left; }
  .studyTagsTooltip .json-to-table tr:nth-child(even) {
    background-color: #fff; }
  .studyTagsTooltip .json-to-table th[scope='col'] {
    background-color: #fff;
    color: #fff; }
  .studyTagsTooltip .json-to-table th[scope='row'] {
    background-color: #d7d9f2; }
  .studyTagsTooltip .json-to-table caption {
    caption-side: bottom; }
